<template>
  <div class="container">
    <div class="py-5 text-center">
       <h2>로그아웃을 위한 화면</h2>
    </div>
    <div class="row">
      <div class="col">
         <button class="w-100 btn btn-danger btn-lg" type="button" @click="logout()">로그아웃</button>
      </div>
      <div class="col">
         <button class="w-100 btn btn-secondary btn-lg" type="button" @click="list()">목록으로</button>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name:'logout-view',
  methods:{
    list(){
      this.$router.push(`/home?page=1&size=9&type=t&keyword=`);
    },
    logout(){
      axios.get('/api/members/logout').then(()=>{
          alert('로그아웃 성공!');
          this.$router.push(`/home?page=1&size=9&type=t&keyword=`);
      })
    }
  },


}

</script>

<style>
.container{
  max-width:560px,

}
</style>
