<template>
  <el-row>
    <el-col
      v-for="(product, i) in data"
      :key="i"
      :span="8"
      :offset="index > 0 ? 2 : 0"
    >
      <el-card :body-style="{ padding: '0px' }">
        <div>
          <img
              :src="`/api/display?fileName=${product.imgsrc}`"
              class="image"
          />
        </div>
        <div style="padding: 14px">
          <span @click="$router.push(`/read/${product.id}?page=${NowPage}&size=${NowSize}&type=${this.type}&keyword=${this.keyword}`)">상품명:{{product.title}}</span>
          <div></div>
          <span style="font-size:15px">가격:{{product.price}}원</span>
          <div class="bottom">
            <time class="time">등록날짜:{{product.regDate}}</time>

          </div>
        </div>
      </el-card>
    </el-col>
  </el-row>
  <div class="mt-10 mb-4" style="padding-top:30px">
      <el-input
          v-model="keyword"
          placeholder="검색할 내용을 입력하세요"
          class="input-with-select"
          style="width:450px"
      >
        <template #prepend>
          <el-select v-model="type" placeholder="Select" style="width: 100px">
            <el-option label="제목" value="t" />
            <el-option label="내용" value="c" />
            <el-option label="제목+내용" value="tc" />
          </el-select>
        </template>
        <template #append>
          <el-button type="success" @click="Search()">Search</el-button>
        </template>
      </el-input>
    </div>

  <el-pagination background layout="prev, pager, next" :page-count="TotalPage"
                 @current-change="pageChange"
                 v-model:currentPage="NowPage"
                 style="justify-content: center;"
  />
</template>


<script>

import axios from "axios";

  export  default {
    name:'Home-View',
    data(){
      return{
        data:[],
        NowPage:this.$route.query.page,
        NowSize:this.$route.query.size,
        TotalPage:0,
        fits:['fill', 'contain', 'cover', 'none', 'scale-down'],
        type:this.$route.query.type,
        keyword:this.$route.query.keyword,


      }
    },
    mounted(){
      if(isNaN(this.NowPage)){
           this.NowPage = 1;
      }
      if(isNaN(this.NowSize)){
        this.NowSize = 9;
      }
      console.log(this.type, this.keyword);
      if(this.type==undefined){
        this.type='';
      }
      if(this.keyword==undefined){
        this.keyword='';
      }

      console.log(this.type, this.keyword);
      axios.get(`/api/products?page=${this.NowPage-1}&size=${this.NowSize}&type=${this.type}&keyword=${this.keyword}`).then(response=>{
        this.data = response.data.responses;
        this.TotalPage = response.data.totalPage;
        this.NowPage = response.data.page+1;
        this.NowSize = response.data.size;

      })
    },
    methods:{
      pageChange(){
        this.$router.push(`/home?page=${this.NowPage}&size=${this.NowSize}&type=${this.type}&keyword=${this.keyword}`);
        axios.get(`/api/products?page=${this.NowPage-1}&size=9&type=${this.type}&keyword=${this.keyword}`).then(response=>{
          this.data=[];
          this.data = response.data.responses;
        })
      },
      Search(){
        axios.get(`/api/products?page=0&size=9&type=${this.type}&keyword=${this.keyword}`).then((response)=>{
          console.log(response);
          this.data=[];
          this.data = response.data.responses;
          this.TotalPage = response.data.totalPage;
          this.NowPage = response.data.page+1;
          this.NowSize = response.data.size;
        })
      }
    }




  }
</script>


<style>
.time {
  font-size: 12px;
  color: #999;
}

.bottom {
  margin-top: 13px;
  line-height: 12px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.button {
  padding: 0;
  min-height: auto;
}

.image {
  width: 100%;
  height: 20vh;
  display: block;
}



</style>